[class*=bg-] {
  background-color: var(--background) !important;
  color: var(--text);
  border-color: rgba(var(--bw-01), var(--bw-01), var(--bw-01), 0.5); }

.bg-dark {
  --background: #201f00;
  --text: white;
  --headers: #d3cc00;
  --links: #d3cc00;
  --bw-01: 255;
  --light-inverse: #908f80;
  --text-light-inverse: white;
  --dark-inverse: #908f80;
  --text-dark-inverse: white;
  --contrast-10: #535000;
  --contrast-20: #868200;
  --contrast-30: #b9b300;
  --contrast-40: #ece500;
  --contrast-50: #fff820;
  --contrast-60: #fffa53;
  --contrast-70: #fffb86;
  --contrast-80: #fffdb9;
  --contrast-90: #fffeec;
  --trans-10: rgba(32, 31, 0, 0.1);
  --trans-20: rgba(32, 31, 0, 0.2);
  --trans-30: rgba(32, 31, 0, 0.3);
  --trans-40: rgba(32, 31, 0, 0.4);
  --trans-50: rgba(32, 31, 0, 0.5);
  --trans-60: rgba(32, 31, 0, 0.6);
  --trans-70: rgba(32, 31, 0, 0.7);
  --trans-80: rgba(32, 31, 0, 0.8);
  --trans-90: rgba(32, 31, 0, 0.9);
  --gray-10: #36351a;
  --gray-20: #4d4c33;
  --gray-30: #63624d;
  --gray-40: #797966;
  --gray-50: #908f80;
  --gray-60: #a6a599;
  --gray-70: #bcbcb3;
  --gray-80: #d2d2cc;
  --gray-90: #e9e9e6; }
